<template>
    <div class="order">
        <el-table
            :data="tableData5"
            style="width: 100%">
            <el-table-column type="expand">
            <template slot-scope="props">
                <el-form label-position="left" inline class="demo-table-expand">
                    <el-form-item label="商品编号">
                        <span>{{ props.row.order_oid }}</span>
                    </el-form-item>
                    <el-form-item label="用户编号">
                        <span>{{ props.row.user_id }}</span>
                    </el-form-item>
                    <el-form-item label="商品类型">
                        <span>{{ props.row.comm_type }}</span>
                    </el-form-item>
                    <el-form-item label="商品名称">
                        <span>{{ props.row.order_name }}</span>
                    </el-form-item>
                    <el-form-item label="房屋面积">
                        <span>{{ props.row.order_per }}</span>
                    </el-form-item>
                    <el-form-item label="预约时间">
                        <span>{{ props.row.make_date }}</span>
                    </el-form-item>
                    <el-form-item label="下单时间">
                        <span>{{ props.row.place_time }}</span>
                    </el-form-item>
                    <el-form-item label="小区地址">
                        <span>{{ props.row.user_addree }}</span>
                    </el-form-item>
                    <el-form-item label="号楼门牌">
                        <span>{{ props.row.user_num }}</span>
                    </el-form-item>
                    <el-form-item label="用户姓名">
                        <span>{{ props.row.user_name }}</span>
                    </el-form-item>
                    <el-form-item label="手机号码">
                        <span>{{ props.row.user_mobile }}</span>
                    </el-form-item>
                    <el-form-item label="支付方式">
                        <span v-if="props.row.play_way == 0">微信支付</span>
                        <span v-if="props.row.play_way == 1">线下支付</span>
                    </el-form-item>
                    <el-form-item label="支付价格">
                        <span>{{ props.row.order_price }}</span>
                    </el-form-item>
                    <el-form-item label="接单人">
                        <span>{{ props.row.project }}</span>
                    </el-form-item>
                </el-form>
            </template>
            </el-table-column>
            <el-table-column
            label="商品编号"
            prop="order_oid">
            </el-table-column>
            <el-table-column
            label="下单时间"
            prop="place_time">
            </el-table-column>
            <el-table-column
            label="商品类型"
            prop="comm_type">
            </el-table-column>
            <el-table-column
            label="支付方式">
                <template slot-scope="scope">
                    <span v-if="scope.row.play_way == 0">微信支付</span>
                    <span v-if="scope.row.play_way == 1">线下支付</span>
                </template>
            </el-table-column>
            <el-table-column
            label="订单状态">
                <template slot-scope="scope">
                    <span v-if="scope.row.order_type == 0">未支付</span>
                    <span v-if="scope.row.order_type == 1">进行中</span>
                    <span v-if="scope.row.order_type == 2">已完成</span>
                    <span v-if="scope.row.order_type == 3">已取消</span>
                </template>
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row.order_oid)">接单</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDone(scope.$index, scope.row.order_oid)">完成</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    v-if="roles == 'admin'"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>
      
<style>
    .order {
        padding: 20px;
    }
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
</style>
      
<script>
export default {
    data() {
        return {
            tableData5: [],
            roles:""
        }
    },
    mounted() {
        this.orderList();   
        this.roles = this.$store.state.userInfo.roles;
    },
    methods: {
        orderList () {
            let url = '/api'+this.$common.order_all;
            this.$http.post(url).then((res)=>{
                console.log(res)
                if ( res.code == '200' ) {
                    this.tableData5 = res.data;
                } else if( res.code == '404' ){
                    sessionStorage.clear();
                    this.$router.push({ path:'/login' });
                }   
            }).catch(data=>{

            })
        },
        handleEdit (index,order_oid) {
            let that = this;
            that.$prompt('请输入接单人姓名', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
            }).then(({ value }) => {
                console.log(value)
                let url = '/api'+that.$common.project;
                let data = ({
                    order_oid:order_oid,
                    project:value
                })
                that.$http.post(url,data).then((res)=>{
                    console.log(res);
                    if(res.code == 1){
                        this.$message({
                            message: '增加接单人成功',
                            type: 'success'
                        });
                    }else {
                        this.$message({
                            message: '增加接单人失败',
                            type: 'success'
                        }); 
                    }  
                }).catch(res=>{
                    this.$message({
                        message: '增加接单人失败',
                        type: 'success'
                    });
                })
            }).catch(() => {
                      
            });
        },
        handleDone (index,order_oid) {
            let that = this;
            this.$confirm('是否已经完成订单', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                let url = '/api'+that.$common.done;
                let data = ({
                    order_oid:order_oid,
                })
                that.$http.post(url,data).then((res)=>{
                    console.log(res);
                    if(res.status == 1){
                        that.$message({
                            message: '完成订单',
                            type: 'success'
                        });
                    }else {
                        that.$message({
                            message: '完成订单失败',
                            type: 'success'
                        }); 
                    }  
                }).catch(res=>{
                    this.$message({
                        message: '完成订单失败',
                        type: 'success'
                    });
                })   
            }).catch(() => {
                          
            });
            
        }
    },
}
</script>


